function Home() {
  return (
    <div className="Main-Panel">
      <h2>Interaktivn mapa sbrnch mst</h2>
      <br />
      <p>
        Mapa sbrnch mst vm pome nalzt nejbli sbrn box, kam mete
        odevzdat odpadn baterie. Zadejte lokalitu, kter vs zajm, a zobraz
        se vm vechna sbrn msta v okol.
      </p>
      <br />
      <div className="img-wrapper-home">
        <img src={require("../images/battery-recycling.png")} />
      </div>
    </div>
  );
}

export default Home;
